const Loading = () => {
    return (
        <div id="component">
            <div id="circle">
            </div>
                <div id="rocket">
                    <img src="/loading_rocket.png" width="150" height="150" />
                </div>

            <style jsx>{`
                #component{
                    position: 'relative',
                    height: '100%',
                    textAlign: 'center',
                }

                #circle{
                    borderRadius: '50%',
                    border: solid white 2px;
                    height: '100px',
                    width: '100px',
                }
            
            `}</style>
        </div>
    );
}

export default Loading